import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
	withStyles,
	Button,
	Card,
	CardContent,
	Checkbox,
	Divider,
	FormControl,
	FormControlLabel,
	TextField,
	Typography,
	Grid,
	Hidden,
	IconButton
} from '@material-ui/core';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Link } from 'react-router-dom';
import InputAdornment from '@material-ui/core/InputAdornment';
import { AccountCircle, Visibility, VisibilityOff } from '@material-ui/icons';
import { userActions } from '../../auth/store/actions/user.actions';
import { withRouter } from 'react-router-dom';
import image from '../../../assets/login1.jpg';
import { styles } from '../index';
import LoadingCircle from '../../components/Progress/loadingCircle';

class AuthLogin extends Component {
	state = {
		email: '',
		password: '',
		remember: true,
		showPassword: false
	};

	static getDerivedStateFromProps(props, state) {
		if (props.userLogin) {
			props.history.push({
				pathname: '/home'
			});
		}
		return null;
	}

	handleChange = event => {
		this.setState({
			[event.target.name]:
				event.target.type === 'checkbox' ? event.target.checked : event.target.value
		});
	};

	handleClickShowPassword = () => {
		this.setState(state => ({ showPassword: !state.showPassword }));
	};

	canBeSubmitted() {
		const { email, password } = this.state;
		return email.length > 0 && password.length > 0;
	}

	handleOnClick = () => {
		this.props.handleLoginUser(this.state.email, this.state.password);
	};

	render() {
		const { classes } = this.props;
		const { email, password, remember } = this.state;

		return (
			<Grid container className={classes.root}>
				<Hidden smDown>
					<Grid item xs={8} className={classes.leftPanel} style={{ height: '100vh ' }}>
						<img src={image} alt='logos' style={{ height: '100vh ' }} />
					</Grid>
				</Hidden>
				<Grid item xs={12} sm={8} md={4} className={classes.rightPanel}>
					<Typography className={classes.titleForm} variant='h6'>
						LOGIN TO YOUR ACCOUNT
					</Typography>
					<form name='loginForm' className={classes.form} noValidate>
						<TextField
							className={classes.textField}
							label='Email'
							autoFocus
							type='email'
							name='email'
							value={email}
							onChange={this.handleChange}
							variant='outlined'
							required
							fullWidth
						/>

						<TextField
							className={classes.textField}
							label='Password'
							type={this.state.showPassword ? 'text' : 'password'}
							name='password'
							value={password}
							onChange={this.handleChange}
							variant='outlined'
							required
							fullWidth
							InputProps={{
								endAdornment: (
									<InputAdornment position='end'>
										<IconButton
											aria-label='Toggle password visibility'
											onClick={this.handleClickShowPassword}
										>
											{this.state.showPassword ? <VisibilityOff /> : <Visibility />}
										</IconButton>
									</InputAdornment>
								)
							}}
						/>

						<div className={classes.divRemember}>
							<FormControl>
								<FormControlLabel
									control={
										<Checkbox name='remember' checked={remember} onChange={this.handleChange} />
									}
									label='Remember Me'
								/>
							</FormControl>

							<Link className={classes.links} to='/auth/forgot-password'>
								Forgot Password?
							</Link>
						</div>

						<Button
							className={classes.buttonSubmit}
							variant='contained'
							color='primary'
							aria-label='LOG IN'
							disabled={!this.canBeSubmitted() || this.props.loadingLogin}
							onClick={() => this.handleOnClick()}
						>
							{this.props.loadingLogin && (
								<CircularProgress size={24} className={classes.buttonProgress} />
							)}
							LOGIN
						</Button>
					</form>

					<div className={classes.separator}>
						<Divider className={classes.divider} />
						<span className={classes.spacer}>OR</span>
						<Divider className={classes.divider} />
					</div>

					<div style={{ paddingTop: '1rem', paddingBottom: '1rem' }}>
						<Link className={classes.links} to='/register'>
							Create an account
						</Link>
					</div>
				</Grid>
			</Grid>
		);
	}
}

const mapStateToProps = state => {
	return {
		loadingLogin: state.authReducers.loadingLogin,
		userLogin: state.authReducers.userLogin
	};
};
const mapDispatchToProps = dispatch => {
	return {
		handleLoginUser: (email, password) => {
			dispatch(userActions.LoginUser({ email, password }));
		}
	};
};

export default withRouter(
	connect(mapStateToProps, mapDispatchToProps)(withStyles(styles, { withTheme: true })(AuthLogin))
);
