import { darken } from '@material-ui/core/styles/colorManipulator';
import { green } from '@material-ui/core/colors';


export const styles = theme => ({
    root: {
        background:
            'linear-gradient(to right, ' +
            theme.palette.primary.dark +
            ' 0%, ' +
            darken(theme.palette.primary.dark, 0.5) +
            ' 100%)',
        color: theme.palette.primary.contrastText,
        display: 'flex',
        minHeight: '100vh',
        [theme.breakpoints.only('sm')]: {
            justifyContent: 'center',
            alignItems: 'center'
        }
    },
    rootCard: {
        '& > div': {
            borderRadius: '12px'
        },
        background:
            'linear-gradient(to right, ' +
            theme.palette.primary.dark +
            ' 0%, ' +
            darken(theme.palette.primary.dark, 0.5) +
            ' 100%)',
        // color: theme.palette.primary.contrastText,
        display: 'flex',
        minHeight: '100vh',
        justifyContent: 'center',
        alignItems: 'center'
    },
    leftPanel: {},
    rightPanel: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        background: 'white',
        padding: '20px 30px'
    },
    links: {
        textDecoration: 'none'
    },
    form: {
        width: '100%',
        justifyContent: 'center',
        flexDirection: 'column',
        display: 'flex'
    },
    buttonSubmit: {
        marginTop: '1rem',
        padding: '6px 16px',
        fontSize: '1rem',
        minWidth: '64px',
        boxSizing: 'border-box',
        lineHeight: '1.75',
        fontWeight: '400',
        borderRadius: '50px',
        textTransform: 'uppercase'
    },
    buttonProgress: {
        color: green[500],
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -12,
        marginLeft: -12
    },

    textField: {
        paddingBottom: '1.6rem'
    },
    titleForm: { color: 'rgba(0, 0, 0, 0.87)', marginBottom: '2rem' },
    divRemember: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        color: 'rgba(0, 0, 0, 0.87)'
    },
    separator: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        marginTop: '1rem',
        marginBottom: '1rem'
    },
    divider: { color: 'rgba(0, 0, 0, 0.87)', width: '3.2rem' },
    spacer: { marginLeft: '.8rem', marginRight: '.8rem' }
});